import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	data() {
		return {
			type: '',
			selects:'',
			status:0	// 0:提交订单，1：查看订单
		}
	},
	components:{
		uniPopup
	},
	onLoad(e){
		this.status = e.status;
	},
	methods: {
		togglePopup(type) {
			this.type = type;
		},
		select(val){
			var _this = this;
			var selects = _this.selects;
			if(selects.indexOf(val)==-1){
				selects += val+',';
			} else {
				selects.replace(val,'');
			}
			_this.selects = selects.substring(0,selects.length-1);
		}
	}
}
